                                        <p>Победителями конкурса признаются участники, набравшие наибольшее количество
                                            баллов и имеющие наиболее высокие позиции в общем рейтинге. Победителем
                                            конкурса
                                            не может быть признан участник, заявка которого набрала менее 3 баллов.</p>
                                        <p>Координационный совет имеет право принять решение, отличное от мнения
                                            экспертов в
