                                            общего рейтинга, обосновав свое решение в соответствии с критериями оценки,
                                            и
                                            занесением соответствующей информации в протокол заседания.</p>
                                        <p>Протокол заседания координационного совета, содержащий информацию об общем
                                            рейтинге участников и победителях конкурса, подписывается председателем и
                                            секретарем координационного совета в течение 10 дней с даты проведения
                                            заседания
                                            координационного совета и направляется в департамент.</p>
